@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,700;0,900;1,200;1,300;1,400;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
    font-family: 'Source Code Pro', monospace;
}



body{font-size:16px}section{padding-top:3rem;padding-bottom:3rem}article.content{height:100vh;overflow:auto}header{min-height:100vh;background:#111;padding:6rem 0;color:#f9f9f9}h1{color:#f9f9f9;font-weight:700}h1 span{display:block;color:#0b73ba}div.sub-h1{background:#0b73ba;color:#f9f9f9;font-family:"Source Sans Pro",sans-serif;font-size:1.5rem;font-weight:700;text-transform:uppercase}h2::after{content:"";display:block;width:0;height:0;border-style:solid;border-width:.5em 1.5em 0 0;border-color:#0b73ba transparent transparent transparent}nav{position:fixed;width:100%;background:url(/static/media/triangle.924a76fb.svg) center center no-repeat;background-size:100% 100%;text-align:center;font-weight:700;transition:all .2s ease-in-out;overflow:hidden}nav .menu-toggle{text-transform:uppercase;font-size:1.2rem;height:3rem;min-width:3rem;cursor:pointer;color:#f9f9f9}nav .menu-toggle .menu-toggle-label-segment{display:block;transition:all .2s ease-in-out}nav .menu-toggle .bars{display:inline-block;width:.7rem;height:.8rem;margin:0 .1rem}nav .menu-toggle .bars .bar{width:100%;height:.2rem;background:#f9f9f9}nav .menu-toggle .bars .bar:nth-of-type(3n+2){margin:.1rem 0}nav ul{transition:all .2s ease-in-out;transform:scale(0.1);padding-bottom:3rem}nav ul li a{font-size:2rem;font-weight:700;color:#f9f9f9;margin:1rem}nav.open{height:100vh;background-color:#111}nav.open~article{overflow-y:hidden}nav.open ul{transform:scale(1)}nav.open .menu-toggle-label-segment{transform:scaleX(0);width:0}nav.closed{height:3rem}#about{background:#f5f9fc}.quick-contact-actions i{color:#a2be72;font-size:1.75rem;margin-right:1rem}.quick-contact-actions a{color:#a2be72;font-weight:700}.quick-contact-actions a:hover{color:#6f8a40}footer{padding:2rem 0;text-align:center;background:#f5f9fc url(/static/media/triangle-white.db9561bc.svg) no-repeat center center;background-size:100% 100%}footer p{font-size:.75rem;color:#353535;font-weight:700;margin:0}#konami-tooltip{background:#f5f9fc;font-size:.85rem;font-weight:700}#konami-tooltip i{display:inline-block}#konami-tooltip i:nth-of-type(3n + 1){color:#a2be72;transform:rotateZ(-70deg);margin-right:-0.3rem}#konami-tooltip i:nth-of-type(3n + 2){color:#f4e53c;transform:rotateZ(10deg) scale(1.2);margin-top:-0.3rem}#konami-tooltip i:nth-of-type(3n){color:#0b73ba;transform:rotateZ(-15deg) scale(0.9);margin-left:-0.3rem}
.contact-code-block{max-width:70rem}.contact-code-block a{color:#f4e53c;font-weight:700}.contact-code-block .code-styled{background:#353535;color:#f9f9f9}.contact-code-block .option-inline{cursor:pointer;color:#fff;background:#f4e53c;background:#a2be72;background:#0b73ba;margin-right:1rem}.contact-code-block .option-inline::before{content:"["}.contact-code-block .option-inline::after{content:"]\a";white-space:pre}span.code-cursor{animation:cursorblink .65s linear infinite}@keyframes cursorblink{0%{color:inherit}50%{color:transparent}}
.skills-technologies figure.tech-badge{padding:0;width:6rem;height:6rem;margin:1rem}.skills-technologies figure.tech-badge img{display:block;max-width:4rem;max-height:4rem}.skills-technologies figure.tech-badge figcaption{font-size:.85rem;font-weight:700}
#konami{position:fixed;top:0;left:0}#konami .konami-layer{position:fixed;top:0;left:0;right:0;bottom:0}#konami .konami-layer .message-box{padding:1rem;color:#f9f9f9;font-weight:700;background:rgba(53,53,53,.5)}#konami .konami-layer .message-box button{border:none;outline:none;transition:all .2s ease-in-out;background:#a2be72;color:#f9f9f9;font-weight:700}#konami .konami-layer .message-box button:hover{background:#536730}
